.wrapper {
  &__btn {
    background: linear-gradient(45deg, rgb(7, 128, 147), rgb(9, 155, 177), rgba(11, 198, 227, 1));
    color: white;
    padding: 10px 70px;
    font-size: 14px;
    letter-spacing: 2.5px;
    position: relative;
    overflow: hidden;
    border: 3px solid #957d00;
    transition: 0.8s;
    border-radius: 2px;
  }
  @apply m-2;
}
.btn {
  &__line {
    position: absolute;
    display: block;
  }
  &__line--top {
    height: 4px;
    width: 100%;
    top: 0;
    left: -100%;
    background: linear-gradient(90deg, transparent, white);
  }
  &__line--right {
    height: 100%;
    width: 4px;
    top: -100%;
    right: 0;
    background: linear-gradient(180deg, transparent, white);
  }
  &__line--bottom {
    height: 4px;
    width: 100%;
    bottom: 0;
    left: 100%;
    background: linear-gradient(270deg, transparent, white);
  }
  &__line--left {
    height: 100%;
    width: 4px;
    bottom: -100%;
    left: 0;
    background: linear-gradient(360deg, transparent, white);
  }
  &:hover {
    background: rgb(5, 114, 130);
    border-color: #c2a205;
    transition: 0.8s;
  }
  &:hover .btn__line--top {
    left: 100%;
    transition: ease-in 0.4s;
  }
  &:hover .btn__line--right {
    top: 100%;
    transition: ease-in 0.4s 0.2s;
  }
  &:hover .btn__line--bottom {
    left: -100%;
    transition: ease-in 0.4s 0.4s;
  }
  &:hover .btn__line--left {
    bottom: 100%;
    transition: ease-in 0.4s 0.6s;
  }
  &:active {
    transition: 0.1s;
    background-color: #01ddffef;
  }
}
